.articleContent {
    text-align: justify;
    margin-bottom: 10px;
    position: relative;
    height: 60px;
    overflow: hidden;
    cursor: pointer;
  }
  .articleContent > p {
    text-indent: 20px;
  }
  .articleContent::before, .articleContent::after, .articleContent > *:first-child {
    opacity: 0.7;
  }
  .articleContent::before {
    content: '';
    float: left;
    width: 5px; height: 100%;
  }
  .articleContent > *:first-child {
    float: right;
    width: 100%;
    margin-left: -5px;
  }
  .articleContent::after {
    content: ' ';
    height: 24px;
    position: relative;
    float: right;
    width: 100px;
    top: -20px;
    left: 100%;
    margin-right: 5px;
    margin-left: -100px;
    text-align: right;
    opacity: 0.95;
  
    background-size: 100% 100%;
    /* 512x1 image, gradient for IE9. Transparent at 0% -> white at 50% -> white at 100%.*/
    background-image: url(data:image/png;base64,);
  
    background: gradient(linear, left top, right top, from(rgba(255, 255, 255, 0)), to(white), color-stop(50%, white));
    background: -o-linear-gradient(to right, rgba(255, 255, 255, 0), white 50%, white);
    background: -webkit-gradient(linear, left top, right top, from(rgba(255, 255, 255, 0)), color-stop(50%, white), to(white));
    background: -webkit-linear-gradient(left, rgba(255, 255, 255, 0), white 50%, white);
    background: -o-linear-gradient(left, rgba(255, 255, 255, 0), white 50%, white);
    background: linear-gradient(to right, rgba(255, 255, 255, 0), white 50%, white);
  }


