.testItem {
    position: relative;
    /* width: 380px; */
    /* height: 160px; */
    /* height: 100%; */
    background: hsla(0,0%,100%,.5);
    background-color: white;
    border-radius: 5px;
    -webkit-box-shadow: 2px 2px 5px #888888;
            box-shadow: 2px 2px 5px #888888;
    /* margin: 20px 0px 0px 10px; */
    padding: 5px;
    color: #000;
    font-weight: 300;
    overflow: hidden;
    display: grid;
    grid-template-rows: 48px 1fr 32px;
  }
  /* .testItem:hover {
    transform: scale(1.1);
    background-color: white;
  } */
  .testItem::before {
    content: '';
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    position: absolute;
    -webkit-filter: blur(10px);
            filter: blur(10px);
    z-index: -1;
    margin: -15px;
  }


