.container-prev, .container-next {
  position: absolute;
  display: block;
  height: 100px;
  width: 100px;
  cursor: pointer;
  background-color: hsla(0,0%,100%,.3);
  border-radius: 50%;
  top: calc(50% - 118px);
}
.container-prev > i, .container-next > i {
  position: absolute;
  top: 50%;
  -webkit-transform-origin: center;
      -ms-transform-origin: center;
          transform-origin: center;
}
.container-prev > i {
