  /* border-radius: 5px; */
  overflow: hidden;
  -webkit-transition: -webkit-transform 1.5s 2s;
  transition: -webkit-transform 1.5s 2s;
  -o-transition: transform 1.5s 2s;
  transition: transform 1.5s 2s;
  transition: transform 1.5s 2s, -webkit-transform 1.5s 2s;
}
.footer:hover>.anchorWrapper, .footer:hover>.anchor-prev, .footer:hover>.anchor-next{
  -webkit-transform: translateY(0px);
      -ms-transform: translateY(0px);
          transform: translateY(0px);
  -webkit-transition: -webkit-transform 1.5s;
  transition: -webkit-transform 1.5s;
  -o-transition: transform 1.5s;
  transition: transform 1.5s;
  transition: transform 1.5s, -webkit-transform 1.5s;
}
.anchor-prev,.anchor-next {
  cursor: pointer;
}
.anchor-prev {
  width: 50px;
  height: 100px;
  -webkit-transform: translateY(40px);
      -ms-transform: translateY(40px);
          transform: translateY(40px);
  position: absolute;
  top: 0;
