 .anchor {
    width: 90px;
    height: 90px;
    margin: 5px 5px;
    border-radius: 5px;
    background-color: #fff;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 50%;
    display: inline-block;
    -webkit-transform: scale(1);
        -ms-transform: scale(1);
            transform: scale(1);
    -webkit-transform-origin: center center;
        -ms-transform-origin: center center;
            transform-origin: center center;
    -webkit-transition: -webkit-transform 0.8s;
    transition: -webkit-transform 0.8s;
    -o-transition: transform 0.8s;
    transition: transform 0.8s;
    transition: transform 0.8s, -webkit-transform 0.8s;
    overflow: hidden;
  }
  .anchor.basic-javascript {
    background-image: url('../../img/basicjavascript.png');
  }
  .anchor.web-security {
    background-image: url('../../img/web-security.png');
  }
  .anchor.javascript-design-pattern {
    background-image: url('../../img/javascript-design-pattern.png');
  }
  .anchor.drawcharts {
    background-image: url('../../img/drawcharts.png');
  }
  .anchor.es6 {
    background-image: url('../../img/es6.png');
  }
  .anchor.es7_8 {
    background-image: url('../../img/es7_8.png');
  }
  .anchor.es9 {
    background-image: url('../../img/es9.png');
  }
  .anchor.css {
    background-image: url('../../img/css.png');
  }
  .anchor.vue {
    background-image: url('../../img/vue.png');
  }
  .anchor.react {
    background-image: url('../../img/react.png');
  }
  .anchor.elementui {
    background-image: url('../../img/elementui.png');
  }
  .anchor.babel {
    background-image: url('../../img/babel.ico');
  }
  .anchor.http1_1 {
    background-image: url('../../img/http1_1.png');
  }
  .anchor.handbyhand {
    background-image: url('../../img/handbyhand.png');
  }
  .anchor.v8 {
    background-image: url('../../img/v8.png');
  }
  .anchor.chrome {
    background-image: url('../../img/chrome.png');
  }
  .anchor.go {
    background-image: url('../../img/go.jpeg');
  }
  .anchor.nodejs {
    background-image: url('../../img/nodejs.jpg');
  }
  .anchor.esnext {
    background-image: url('../../img/esnext.png');
  }
  
  .anchor:hover {
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
    -webkit-transform-origin: center center;
        -ms-transform-origin: center center;
            transform-origin: center center;
    -webkit-transition: -webkit-transform 0.8s;
    transition: -webkit-transform 0.8s;
    -o-transition: transform 0.8s;
    transition: transform 0.8s;
    transition: transform 0.8s, -webkit-transform 0.8s;
    border: 3px solid rgb(215, 202, 153);
    /* animation: test11 1s normal 0s infinite; */
  }
  .anchor.active {
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
    -webkit-transform-origin: center center;
        -ms-transform-origin: center center;
            transform-origin: center center;
    -webkit-transition: -webkit-transform 0.8s;
    transition: -webkit-transform 0.8s;
    -o-transition: transform 0.8s;
    transition: transform 0.8s;
    transition: transform 0.8s, -webkit-transform 0.8s;
    border: 3px solid rgb(215, 202, 153);
    -webkit-animation: test11 2s normal 0s infinite;
            animation: test11 2s normal 0s infinite;
  }
  @-webkit-keyframes test11 {
      0% {
        -webkit-filter: brightness(50%);
                filter: brightness(50%)
      }
      50% {
        -webkit-filter: brightness(110%);
                filter: brightness(110%)
      }
      100% {
        -webkit-filter: brightness(50%);
                filter: brightness(50%)
      }
  }
  @keyframes test11 {
      0% {
        -webkit-filter: brightness(50%);
                filter: brightness(50%)
      }
      50% {
        -webkit-filter: brightness(110%);
                filter: brightness(110%)
      }
      100% {
        -webkit-filter: brightness(50%);
                filter: brightness(50%)
      }
  }
  .anchor:first-child {
    margin-left: 5px;
  }
  .anchor:last-child {
    margin-right: 5px;
  }
  .custom-tooltip {
    opacity: 0.7;
    /* background-color: hsla(0,0%,100%,.3); */
  }


