.articleFooter {
    grid-row-start: 3;
    grid-row-end: 4;
    display: grid;
    grid-template-columns: 32px 1fr 140px;
    /* text-align: right; */
  }
  .articleAvatar {
    grid-column-start: 1;
    grid-column-end: 2;
    border-radius: 32px;
    -webkit-transform: translateY(1.5px);
        -ms-transform: translateY(1.5px);
            transform: translateY(1.5px);
  }
  .articleDate {
    grid-column-start: 2;
    grid-column-end: 3;
    line-height: 32px;
    text-align: left;
    font-size: 16px;
    padding: 0 5px;
    vertical-align: bottom;
  }
  .articleRate {
    margin: 0 auto;
    grid-column-start: 3;
    grid-column-end: 4;
  }


